  <template>
 	<view>
 		<!-- 状态栏 -->
 		<view class="status" :style="{position:headerPosition}"></view>
 		<!-- 漂浮头部 -->
 		<view class="header" style="margin-top:42px" >
 			<view class="scan">
 				<!-- <view class="icon scan" @tap="scan"></view> -->
				<!-- <uni-icons type="location-filled" color="white" size="20"></uni-icons> -->
				<!-- <view v-if="userInfo.managerPart" style="font-size: 12;">
					{{userInfo.managerPart}}
				</view>	 -->
				<view  style="margin-left: 25px;">
					 <uni-data-select
						  v-model="value"
						  :localdata="range"
						  @change="change"
						  placeholder="地址"
						></uni-data-select>
				</view>	
 			</view>
 			<view style="margin-left: 35px;" class="input">
 				<view class="icon search"></view>
 				<input placeholder="热点新闻" @tap="toSearch()" />
 			</view>
 			<!-- <view class="menu">
 				<image mode="widthFix" src="/static/HM-shophome/face.png"></image>
 			</view> -->
 		</view>
 		<!-- 占位 -->
 		<view class="place"></view>
 		<!-- 轮播图 -->
 		<view class="swiper-view">
 			<swiper class="swiper" indicator-dots="true" autoplay="true" circular="true" indicator-active-color="#ffffff">
 				<swiper-item v-for="swiper in swiperList" :key="swiper.sid" @tap="toSwiper(swiper)">
 					<image mode="aspectFill" :src="swiper.img"></image>
 				</swiper-item>
 			</swiper>
 			<view class="keep-out"></view>
 		</view>
 		<!-- 分类轮播 -->
 		<view class="category">
 			<view class="box" style="padding: 0 10px;">
 				<swiper	class="swiper" duration="300" :style="{ height: categoryHeight }" @change="categoryChange">
 					<swiper-item v-for="(page, pageindex) in categoryList" :key="pageindex" >
 						<view class="category-list">
 							<view class="icon" v-for="category in page" :key="category.cat_id" @tap="toCategory(category)">
 								<image mode="widthFix" :src="category.img" @load="categoryImgLoad"></image>
 								<view>{{ category.title }}</view>
 							</view>
 						</view>
 					</swiper-item>
 				</swiper>
 			</view>
 		</view>
 		<!-- 推荐 -->
 		<view class="pick">
 			<view class="box">
 				<view class="title">
 					<view class="big">文章推荐</view>
 				</view>
 			</view>
 		</view>
 		<!-- 商品列表 -->
 		<view class="goods-list">
 			<view class="product-list">
 				<view class="product" v-for="paper in paperList" :key="paper.docid" @tap="toGoods(paper)">
 					<image mode="widthFix" :src="paper.pic_url"></image>
 					<view class="name">{{paper.doctitle}}</view>
 					<view class="info">
 						<view class="price"><uni-icons type="heart-filled" color="red" size="18"></uni-icons></view>
 						<view class="slogan">{{paper.doc_creator_name}}</view>
 					</view>
 				</view>
 			</view>
 			<view class="loading-text">{{loadingText}}</view>
 		</view>
		<view>
			<zwy-popup :ishide="ishide">dsa</zwy-popup>
		</view>
 	</view>
 </template>
 <script>
import { mapState, mapMutations } from 'vuex';
 export default {
	computed:{
	 	...mapState(['userInfo']),
	},
 	data() {
 		return {
			value: 0,
			ishide:false,
			range: [],
 			//轮播
 			swiperList:[
 				{sid:0,src:'自定义src0',img:'/static/HM-shophome/swiper-img/3.jpg'},
 				{sid:1,src:'自定义src1',img:'/static/HM-shophome/swiper-img/1.jpg'},
 				{sid:2,src:'自定义src2',img:'/static/HM-shophome/swiper-img/2.jpg'},
 				{sid:3,src:'自定义src3',img:'/static/HM-shophome/swiper-img/3.jpg'}
 			],
 			//分类
 			categoryList: [
 				[//第一页
 					{ cat_id: 0, img: '/static/HM-shophome/category-img/0.png', title: '生态产品' },
 					{ cat_id: 1, img: '/static/HM-shophome/category-img/1.png', title: 'XXXX' },
 					{ cat_id: 2, img: '/static/HM-shophome/category-img/2.png', title: '民宿' },
 					{ cat_id: 3, img: '/static/HM-shophome/category-img/3.png', title: '租房租地' },
 					{ cat_id: 4, img: '/static/HM-shophome/category-img/4.png', title: '农村美食' },
 					{ cat_id: 5, img: '/static/HM-shophome/category-img/5.png', title: '农村投资' },
 					{ cat_id: 6, img: '/static/HM-shophome/category-img/6.png', title: '定点帮扶' },
 					{ cat_id: 7, img: '/static/HM-shophome/category-img/7.png', title: '特产' },
 				]
 			],
 			//猜你喜欢列表
 			paperList:[],
 			categoryHeight: '120px',
 			currentPageindex: 0,
 			headerPosition:"fixed",
 			loadingText:"正在加载..."
 			
 		};
 	},
 	onLoad() {
 		// TODO
 		this.$api.get('/api/part/listpart').then(res => {
 			this.range = res.data.data.parts
 		});
		this.getPages()
 	},
 	onPageScroll(e){
 		//兼容iOS端下拉时顶部漂移
 		if(e.scrollTop>=0){
 			this.headerPosition = "fixed";
 		}else{
 			this.headerPosition = "absolute";
 		}
 	},
 	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
 	onPullDownRefresh() {
         setTimeout(function () {
             uni.stopPullDownRefresh();
         }, 1000);
     },
 	onReady() {
	},
 	methods: {
		getPages(){
			let httpData = {
				nums:6
			}
			this.$api.post('/api/doc/get_recommend_doc',httpData).then(res => {
				this.paperList = res.data.data.docs
				console.log(res)
			});
		},
		change(event){
			// 申请认证
		console.log(event)
		// this.ishide = true
		},
 		//扫一扫
 		// scan(){
 		// 	uni.scanCode({
 		// 		success:(res)=>{
 		// 			uni.showToast({title: '条码内容：' + res.result});
 		// 		}
 		// 	});
 		// },
 		//搜索跳转
 		toSearch(){
 			uni.showToast({title: "建议跳转到新页面做搜索功能"});
 		},
 		//轮播图跳转
 		toSwiper(e){
 			uni.showToast({title: e.src});
 		},
 		//分类跳转
 		toCategory(e){
 			uni.showToast({title: e.title});
 		},
 		toGoods(e){
 			uni.showToast({title: '文章'+e.doctitle});
 		},
 		//更新分类指示器
 		categoryChange(event) {
 			this.currentPageindex = event.detail.current;
 		},
 		//分类图片加载完毕
 		categoryImgLoad(e){
 			this.categoryImg = this.categoryImg?this.categoryImg+1:1;
 			//完成加载11个分类图片开始计算分类高度，若分类图片不足10个则修改此处的10。
 			if(this.categoryImg==10){
 				this.getCategoryHeight();
 			}
 		},
 		//更新分类高度
 		getCategoryHeight() {
 			let view = uni.createSelectorQuery().select('.category-list');
 			view.fields(
 				{
 					size: true
 				},
 				data => {
 					this.categoryHeight = data.height + 'px';
 				}
 			).exec();
 		}
 	}
 };
 </script>
 <style lang="scss">
 @font-face {font-family:"HMfont-home";src:url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');}
 .icon {
 	font-family:"HMfont-home" !important;
 	font-size:60upx;
 	font-style:normal;
 	color:#ffffff;
 	&.scan {
 		&:before{content:"\e69a";}
 	}
 	&.menu {
 		&:before{content:"\e62b";}
 	}
 	&.search {
 		&:before{content:"\e628";}
 	}
 }
 page {
 	background-color: #fff;
 }
 .status {
 	width: 100%;
 	height: 0;
 	/*  #ifdef  APP-PLUS  */
 	height: var(--status-bar-height);
 	/*  #endif  */
 	background-color: #ff570a;
 	position: fixed;
 	top: 0;
 	z-index: 999;
 }
 .header {
 	width: 100%;
 	height: 100upx;
 	background-color: #ff570a;
 	display: flex;
 	position: fixed;
 	top: 0;
 	/*  #ifdef  APP-PLUS  */
 	top: var(--status-bar-height);
 	/*  #endif  */
 	
 	z-index: 996;
 	.scan {
 		width: 100upx;
 		height: 100upx;
 		flex-shrink: 1;
 		display: flex;
 		justify-content: center;
 		align-items: center;
 	}
 	.input {
 		width: calc(100% - 200upx);
 		display: flex;
 		justify-content: center;
 		align-items: center;
 		position:relative;
 		input {
 			width: calc(100% - 60upx);
 			height: 60upx;
 			background-color: #ffffff;
 			border-radius: 60upx;
 			padding-left: 60upx;
 			font-size: 30upx;
 			
 		}
 		.icon{
 			width: 60upx;
 			height: 60upx;
 			position: absolute;
 			color: #a18090;
 			z-index: 996;
 			top: 20upx;
 			left: 0;
 			font-size: 40upx;
 			display: flex;
 			justify-content: center;
 			align-items: center;
 		}
 	}
 	.menu {
 		width: 100upx;
 		height: 100upx;
 		flex-shrink: 1;
 		display: flex;
 		justify-content: center;
 		align-items: center;
 		image{
 			width: 60upx;
 			height: 60upx;
 			border-radius: 60upx;
 		}
 	}
 }
 .place{
 	/*  #ifdef  APP-PLUS  */
 	margin-top: var(--status-bar-height);
 	/*  #endif  */
 	background-color: #ff570a;
 	height: 100upx;
 }
 .swiper-view {
 	.swiper {
 		width: 100%;
 		height: 280upx;
 		image {
 			width: 100%;
 			height: 280upx;
 		}
 	}
 	.keep-out {
 		width: 100%;
 		height: 30upx;
 		border-radius: 100% 100% 0 0;
 		background-color: #ff570a;
 		margin-top: -15upx;
 		position: absolute;
 	}
 }
 .category {
 	width: 95%;
 	padding: 2.5vw 2.5vw;
 	background-color: #ff570a;
 	.box {
 		width: 100%;
 		border-radius: 20upx;
 		background-color: #ffffff;
 		.dots {
 			display: flex;
 			justify-content: center;
 			height: 15upx;
 			width: 100%;
 			view{
 				width: 30upx;
 				height: 5upx;
 				background-color: rgba(0, 0, 0, 0.2);
 				&.active {
 					background-color: #ff570a;
 				}
 			}
 		}
 		.swiper {
 			width: 100%;
 			padding: 10upx 0;
 			.uni-swiper-dot {
 				width: 20upx;
 			}
 			.category-list {
 				width: 100%;
 				height: auto;
 				display: flex;
 				justify-content: flex-start;
 				padding: 10upx 0;
 				flex-flow: wrap;
 				.icon {
 					width: 20%;
 					display: flex;
 					flex-flow: wrap;
 					justify-content: center;
 					font-size: 22upx;
 					color: #666;
 					image {
 						width: 70%;
 						height: 13.3vw;
 					}
 					view{
 						width: 100%;
 						display: flex;
 						justify-content: center;
 					}
 				}
 			}
 		}
 	}
 }
 .pick{
 	width: 95%;
 	padding: 0 2.5vw 2.5vw 2.5vw;
 	background: linear-gradient(to bottom, #ff570a 0%,#ffffff 105%);
 	.box{
 		width: 100%;
 		border-radius: 20upx;
 		background-color: #ffffff;
 		.title{
 			display: flex;
 			justify-content: flex-start;
 			align-items: flex-end;
 			height: 60upx;
 			border-bottom: solid 1upx #eee;
 			padding-bottom: 10upx;
 			.big{
 				font-size: 34upx;
 				padding-left: 20upx;
 				color: #46434f;
 			}
 			.small{
 				font-size: 24upx;
 				padding-left: 20upx;
 				color: #827f8b;
 			}
 		}
 		.product-list{
 			padding: 15upx 20upx 15upx 20upx;
 			column-count: 2;
 			column-width: 50%;
 			column-gap: 10upx;
 			>view{
 				display: flex;
 				background-color: #f8f8f8;
 				position:relative;
 				&:nth-child(1){
 					.price{
 						color: #e65339;
 						font-size: 30upx;
 						position: absolute;
 						bottom: 8upx;
 						left: 8upx;
 					}
 					.slogan{
 						color: #807c87;
 						font-size: 30upx;
 						position: absolute;
 						bottom: 8upx;
 						right: 8upx;
 					}
 					image{
 						width: 100%;
 						height: 0;
 					}
 				}
 				&:nth-child(2),&:nth-child(3){
 					image{
 						width: calc(50% - 5upx);
 						height: 0;
 					}
 					.price{
 						position: absolute;
 						top: 25%;
 						left: 55%;
 						color: #e65339;
 						font-size: 30upx;
 					}
 					.slogan{
 						position: absolute;
 						top: 60%;
 						left: 55%;
 						color: #807c87;
 						font-size: 20upx;
 					}
 				}
 				&:nth-child(2){
 					margin-bottom: 10upx;
 				}
 			}
 		}
 	}
 }
 .banner{
 	image{
 		width: 100%;
 	}
 }
 .goods-list{
 	background-color: #f4f4f4;
 	.title{
 		width: 100%;
 		display: flex;
 		justify-content: center;
 		align-items: center;
 		height: 60upx;
 		color: #979797;
 		font-size: 24upx;
 	}
 	.loading-text{
 		width: 100%;
 		display: flex;
 		justify-content: center;
 		align-items: center;
 		height: 60upx;
 		color: #979797;
 		font-size: 24upx;
 	}
 	.product-list{
 		width: 95%;
 		padding: 0 2.5% 2.5vw 2.5%;
 		display: flex;
 		justify-content: space-between;
 		flex-wrap: wrap;
 		.product{
 			width: 48.75%;
 			border-radius: 20upx;
 			background-color: #fff;
 			margin: 0 0 15upx 0;
 			image{
 				width: 100%;
 				border-radius: 20upx 20upx 0 0;
 			}
 			.name{
 				width: 92%;
 				padding: 10upx 4%;
 				display: -webkit-box;
 				-webkit-box-orient: vertical;
 				-webkit-line-clamp: 2;
 				text-align: justify;
 				overflow: hidden;
 				font-size: 30upx;
 			}
 			.info{
 				display: flex;
 				justify-content: space-between;
 				align-items: flex-end;
 				width: 92%;
 				padding: 10upx 4% 10upx 4%;
 				
 				.price{
 					color: #e65339;
 					font-size: 30upx;
 					font-weight: 600;
 				}
 				.slogan{
 					color: #807c87;
 					font-size: 24upx;
 				}
 			}
 		}
 		
 	}
 }
 </style>
 